<template>
  <div class="company-profile">
    <div class="content" v-html="companyProfile"></div>
  </div>
</template>

<script>
import { getCompanyProfile } from '@/api/company';
import { ERR_OK } from '@/api/config';

export default {
  data() {
    return {
      companyProfile: ''
    };
  },
  created() {
    this._getProfile();
  },
  methods: {
    _getProfile() {
      getCompanyProfile().then((res) => {
        if (res.data.code === ERR_OK) {
          this.companyProfile = res.data.data.content;
        } else {
          this.$dialog.alert({
            mes: res.data.msg,
            callback() {}
          });
        }
      }).catch((err) => {
        console.error(err);
      });
    }
  }
};
</script>

<style scoped lang="stylus">
.company-profile
  position absolute
  top 0
  bottom 0
  left 0
  right 0
  &::-webkit-scrollbar
    display none
  .content
    padding 0.2rem
    text-align justify
    font-size 0.34rem
</style>



